.chat-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f9f9f9; /* Light background color */
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #4CAF50; /* Header color */
}

.logo {
  height: 50px; /* Adjust as needed */
  margin-right: 10px;
}

.messages-container {
  flex: 1;
  padding: 10px;
  overflow-y: auto;
  border: 1px solid #ccc; /* Add a border */
}

.message {
  margin: 10px 0;
}

.user-text {
  background-color: #e1f5fe; /* User message background */
  padding: 10px;
  border-radius: 5px;
  text-align: right;
}

.bot-message {
  display: flex;
  align-items: center;
}

.chatbot-avatar {
  height: 40px; /* Adjust as needed */
  margin-right: 10px;
}

.bot-text {
  background-color: #ffe0b2; /* Bot message background */
  padding: 10px;
  border-radius: 5px;
}

.input-container {
  display: flex;
  padding: 10px;
  border-top: 1px solid #ccc; /* Add a border */
}

.input {
  flex: 1;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.ask-button, .voice-button {
  margin-left: 10px;
  padding: 10px;
  border-radius: 5px;
  background-color: #4CAF50; /* Button color */
  color: white;
  border: none;
  cursor: pointer;
}

.ask-button:hover, .voice-button:hover {
  background-color: #45a049; /* Button hover color */
}
